import { Link, useNavigate } from 'react-router-dom'
import { Card, Breadcrumb, Form, Button, Radio, DatePicker, Select, Table, Tag, Space, Popconfirm, message } from 'antd'
import 'moment/locale/zh-cn'
import locale from 'antd/es/date-picker/locale/zh_CN'
import './index.scss'
import { EditOutlined, DeleteOutlined } from '@ant-design/icons'

import img404 from '@/assets/error.png'
import { useEffect, useState } from 'react'
import { http } from '@/utils'
import { useStore } from '@/store'
import { observer } from 'mobx-react-lite'

const { Option } = Select
const { RangePicker } = DatePicker

const ArticleContent = () => {
    const { channelStore: { channelList } } = useStore()
    // 
    // const [channelList, setChannelList] = useState([])


    /* useEffect(() => {
      const loadChannelList = async () => {
        const res = await http.get('/channels')
        setChannelList(res.data.channels)
      }
      loadChannelList()
    }, []) */

    // 
    const [artList, setArtList] = useState({
        list: [],// 
        count: 0 // 
    })
    // 
    const [params, setParams] = useState({
        page: 1,
        per_page: 10,
        update: true
    })

    // 
    // 
    useEffect(() => {
        const loadList = async () => {
            const res = await http.get('/mp/articles', { params })
            const { results: list, total_count: count } = res.data
            setArtList({
                list,
                count
            })
        }
        loadList()
    }, [params])


    const onFinish = (values) => {
        const { channel_id, date, status } = values
        // 
        /* if (!(channel_id && date)) {
          return notification['warning']({
            message: ' ',
            description:
              `${channel_id ? '' : ' '} ${date ? '' : ' '}`,
          });
        } */
        // 
        const newParams = {}
        if (status !== -1) {
            newParams.status = status
        }
        if (channel_id) {
            newParams.channel_id = channel_id
        } else {
            newParams.channel_id = ''
        }
        // console.log('date', date);
        if (date) {
            const prev = date[0].format('YYYY-MM-DD')
            const end = date[1].format('YYYY-MM-DD')
            if (prev >= end) {
                return message.error('');
            }
            newParams.begin_pubdate = prev
            newParams.end_pubdate = end
        }
        // params
        // console.log('');
        setParams({ ...params, ...newParams })
        // setParams(params => { return Object.assign(params, newParams) })
    }

    const navigate = useNavigate()
    const goPublish = (data) => {
        navigate(`/article/compile?id=${data.id}`)
    }

    const columns = [
        {
            title: '',
            dataIndex: 'cover',
            width: 120,
            render: cover => {
                return <img src={cover.images[0] || img404} width={80} height={60} alt="" />
            }
        },
        {
            title: '',
            dataIndex: 'title',
            width: 220
        },
        {
            title: '',
            dataIndex: 'status',
            render: data => <Tag color="green"></Tag>
        },
        {
            title: '',
            dataIndex: 'pubdate'
        },
        {
            title: '',
            dataIndex: 'read_count'
        },
        {
            title: '',
            dataIndex: 'comment_count'
        },
        {
            title: '',
            dataIndex: 'like_count'
        },
        {
            title: '',
            render: data => {
                return (
                    <Space size="middle">
                        <Button
                            type="primary"
                            shape="circle"
                            icon={<EditOutlined />}
                            onClick={() => goPublish(data)}
                        />
                        <Popconfirm
                            onConfirm={() => delArticle(data)}
                            title=""
                            okText=""
                            cancelText="">
                            <Button
                                type="primary"
                                danger
                                shape="circle"
                                icon={<DeleteOutlined />}
                            />
                        </Popconfirm>
                    </Space>
                )
            }
        }
    ]
    const delArticle = async (data) => {
        // console.log('data', data);
        await http.delete(`/mp/articles/${data.id}`)
        setParams(params => ({
            ...params,
            update: !params.update
        }))
    }
    /* const data = [
      {
        id: '8218',
        comment_count: 0,
        cover: {
          images: ['http://geek.itheima.net/resources/images/15.jpg'],
        },
        like_count: 0,
        pubdate: '2019-03-11 09:00:00',
        read_count: 2,
        status: 2,
        title: 'wkwebviewh5'
      }
    ] */

    const pageChange = (page) => {
        setParams({
            ...params,
            page
        })
    }
    return (
        <div>
            {/*  */}
            <Card
                title={
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item>
                            <Link to="/"></Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item></Breadcrumb.Item>
                    </Breadcrumb>
                }
                style={{ marginBottom: 20 }}
            >
                <Form
                    onFinish={onFinish}
                    initialValues={{ status: null }}
                >
                    <Form.Item
                        label=""
                        name="status"
                    >
                        <Radio.Group>
                            <Radio value={null}></Radio>
                            <Radio value={0}></Radio>
                            <Radio value={1}></Radio>
                            <Radio value={2}></Radio>
                            <Radio value={3}></Radio>
                        </Radio.Group>
                    </Form.Item>

                    <Form.Item label="" name="channel_id">
                        <Select
                            placeholder=""
                            style={{ width: 120 }}
                        >
                            <Option value={null}></Option>
                            {/* <Option value="lucy">Lucy</Option> */}
                            {channelList.map(item => <Option key={item.id} value={item.id}>{item.name}</Option>)}
                        </Select>
                    </Form.Item>

                    <Form.Item label="" name="date">
                        {/* locale */}
                        <RangePicker locale={locale}></RangePicker>
                    </Form.Item>

                    <Form.Item>
                        <Button type="primary" htmlType="submit" style={{ marginLeft: 80 }}>
                            
                        </Button>
                    </Form.Item>
                </Form>
            </Card>
            {/*  */}
            <Card title={` ${artList.count} `}>
                <Table
                    rowKey="id"
                    columns={columns}
                    dataSource={artList.list}
                    pagination={{
                        pageSize: params.per_page,
                        total: artList.count,
                        onChange: pageChange
                    }}
                />
            </Card>
        </div>
    )
}

export default observer(ArticleContent)